ите как время, когда вы начали работать над темой, которая привела вас к Нобелевской премии!"}}):_vm._e(),(_vm.STEP_STATE === 12 && _vm.winner == 'mastermind')?_c('Result',{key:13,attrs:{"name":"mastermind","src":"mastermind.gif","title":"Вдохновитель","desc":"2021 год откроет в вас талант Вдохновителя. Наверняка уже в уходящем году вы заметили, что ваше мнение в виртуальных созвонах в Zoom обретало какой-то новый вес. Все так — вы чутко и проницательно видите лучшее в людях, умеете найти единственно правильные слова, которые вселяют уверенность в своих силах и возможности любых невозможных свершений. Воодушевить близких на тотальную смену обстановки? Собрать удаленную команду, говорящую на разных языках и нацеленную на успех общего дела, — может быть, запуск нового «единорога», а может быть ... постановку оперы? Вы удивитесь, сколько единомышленников соберет любое ваше начинание! Кстати, вам уже позвонил Юрий Башмет с предложением совместного проекта?"}}):_vm._e()],1)],1)}
